body {
  background-color: #8a858490;
  font-family: "Arial", sans-serif;
  color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  /* padding: 3rem; */
  box-sizing: border-box;
}

#root {
  margin: 40px 40px;
  padding: -10px 0px;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.918);
  width: 100%;
  max-width: 1200px;
  margin: 20rem;
  box-sizing: border-box;
}

.weather-container {
  margin-bottom: 2rem;
  background-color: #ffffffac;
  border-radius: 20px;
  box-shadow: 0 20px 20px rgba(27, 161, 201, 0.1);
  padding: 2rem;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 20px;
  transition: 1s ease;
}

.weather-container:hover {
  transform: translateY(2px);
}

.weather-main {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 30rem;
}

.weather-main h1 {
  font-size: 72px;
  margin: 0;
}

.weather-main p {
  font-size: 24px;
  margin: 10px 0;
}

.weather-details {
  text-align: left;
  flex-direction: column;
  display: flex;
  width: 100%;
  margin-left: -26rem;
}

.weather-icon {
  height: 6rem;
  width: 7rem;
}

.weather-details img {
  width: 15px;
  height: 15px;
  margin-top: 1rem;
  padding: 2px 10px;
}

.weather-details div {
  text-align: left;
}

.weather-footer {
  margin: 2rem 2rem;
  background-color: #ffffff3d;
  border-radius: 5px;
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
}

.foot-h3 {
  padding: 10px 50px;
}

.weather-footer div {
  outline: none;
  border-radius: 20px;
  margin: 3px;
  padding: 10px 30px;
  text-align: center;
  box-shadow: 0 4px 10px rgba(57, 103, 196, 0.1);
  transition: border 0.3s ease;
  border: 1px solid transparent;
}

.weather-footer div:hover {
  transform: translateY(-2px);
  border-color: #333;
}

.search-form {
  display: flex;
  justify-content: center;
  margin-bottom: -70px;
  margin-top: 18px;
}

.search-input {
  width: 15rem;
  padding: 10px;
  font-size: 16px;
  border: 2px solid #ccc;
  border-radius: 20px 0px 0px 20px;
  outline: none;
  transition: border-color 1s ease;
}

.search-input:focus {
  border-color: #007bff;
}

.search-button {
  margin-left: 6px;
  padding: 10px 20px;
  font-size: 16px;
  border: 2px solid #007bff;
  background-color: #007bff;
  color: white;
  border-radius: 0 20px 20px 0;
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s, border-color 0.3s;
}

.search-button:hover {
  background-color: #0056b3;
  border-color: #0056b3;
}

.container {
  width: 250px;
  height: 250px;
  padding: 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.cloud {
  width: 250px;
}

.front {
  padding-top: 45px;
  margin-left: 25px;
  display: inline;
  position: absolute;
  z-index: 11;
  animation: clouds 8s infinite;
  animation-timing-function: ease-in-out;
}

.back {
  margin-top: -30px;
  margin-left: 150px;
  z-index: 12;
  animation: clouds 12s infinite;
  animation-timing-function: ease-in-out;
}

.right-front {
  width: 45px;
  height: 45px;
  border-radius: 50% 50% 50% 0%;
  background-color: #4c9beb;
  display: inline-block;
  margin-left: -25px;
  z-index: 5;
}

.left-front {
  width: 65px;
  height: 65px;
  border-radius: 50% 50% 0% 50%;
  background-color: #4c9beb;
  display: inline-block;
  z-index: 5;
}

.right-back {
  width: 50px;
  height: 50px;
  border-radius: 50% 50% 50% 0%;
  background-color: #4c9beb;
  display: inline-block;
  margin-left: -20px;
  z-index: 5;
}

.left-back {
  width: 30px;
  height: 30px;
  border-radius: 50% 50% 0% 50%;
  background-color: #4c9beb;
  display: inline-block;
  z-index: 5;
}

.sun {
  width: 120px;
  height: 120px;
  background: -webkit-linear-gradient(to right, #fcbb04, #fffc00);
  background: linear-gradient(to right, #fcbb04, #fffc00);
  border-radius: 60px;
  display: inline;
  position: absolute;
}

.sunshine {
  animation: sunshines 2s infinite;
}

@keyframes sunshines {
  0% {
    transform: scale(1);
    opacity: 0.6;
  }

  100% {
    transform: scale(1.4);
    opacity: 0;
  }
}

@keyframes clouds {
  0% {
    transform: translateX(15px);
  }

  50% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(15px);
  }
}

.time {
  margin-right: 50em;
  position: relative;
  padding: 27px 50px;
  margin-bottom: -20px;
}

.forecast-image {
  height: 4rem;
}

@media (max-width: 768px) {
  .weather-container {
    grid-template-columns: 1fr;
    padding: 1rem;
  }

  .weather-main h1 {
    font-size: 48px;
  }

  .weather-main p {
    font-size: 18px;
  }

  .weather-details {
    margin-left: 0;
  }

  .search-form {
    flex-direction: column;
    margin-bottom: 0;
  }

  .search-input {
    width: 100%;
    margin-bottom: 0.5rem;
    border-radius: 20px;
  }

  .search-button {
    width: 100%;
    border-radius: 20px;
  }

  .weather-footer {
    flex-direction: column;
  }

  .weather-footer div {
    width: 100%;
    margin: 0.5rem 0;
  }
}

@media (max-width: 480px) {
  .weather-main h1 {
    font-size: 36px;
  }

  .weather-main p {
    font-size: 16px;
  }

  .weather-container {
    padding: 1rem;
  }

  .search-input {
    font-size: 14px;
  }

  .search-button {
    font-size: 14px;
  }

  .weather-footer {
    margin: 1rem;
  }
}

.loader {
  width: 130px;
  height: 170px;
  position: relative;
  font-family: inherit;
}

.loader::before,
.loader::after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  bottom: 30px;
  left: 15px;
  z-index: 1;
  border-left: 50px solid transparent;
  border-right: 50px solid transparent;
  border-bottom: 20px solid #1b2a33;
  transform: scale(0);
  transition: all 0.2s ease;
}

.loader::after {
  border-right: 15px solid transparent;
  border-bottom: 20px solid #162229;
}

.loader .getting-there {
  width: 120%;
  text-align: center;
  position: absolute;
  bottom: 0;
  left: -7%;
  font-size: 12px;
  letter-spacing: 2px;
  color: white;
}

.loader .binary {
  width: 100%;
  height: 140px;
  display: block;
  color: white;
  position: absolute;
  top: 0;
  left: 15px;
  z-index: 2;
  overflow: hidden;
}

.loader .binary::before,
.loader .binary::after {
  font-family: "Lato";
  font-size: 24px;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.loader .binary:nth-child(1)::before {
  content: "0";
  animation: a 1.1s linear infinite;
}

.loader .binary:nth-child(1)::after {
  content: "0";
  animation: b 1.3s linear infinite;
}

.loader .binary:nth-child(2)::before {
  content: "1";
  animation: c 0.9s linear infinite;
}

.loader .binary:nth-child(2)::after {
  content: "1";
  animation: d 0.7s linear infinite;
}

.loader.JS_on::before,
.loader.JS_on::after {
  transform: scale(1);
}

@keyframes a {
  0% {
    transform: translate(30px, 0) rotate(30deg);
    opacity: 0;
  }

  100% {
    transform: translate(30px, 150px) rotate(-50deg);
    opacity: 1;
  }
}

@keyframes b {
  0% {
    transform: translate(50px, 0) rotate(-40deg);
    opacity: 0;
  }

  100% {
    transform: translate(40px, 150px) rotate(80deg);
    opacity: 1;
  }
}

@keyframes c {
  0% {
    transform: translate(70px, 0) rotate(10deg);
    opacity: 0;
  }

  100% {
    transform: translate(60px, 150px) rotate(70deg);
    opacity: 1;
  }
}

@keyframes d {
  0% {
    transform: translate(30px, 0) rotate(-50deg);
    opacity: 0;
  }

  100% {
    transform: translate(45px, 150px) rotate(30deg);
    opacity: 1;
  }
}

.time-date {
  margin-top: 3px;
  margin-left: 3.5rem;
}
